.form {
    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 235px);
        grid-template-rows: repeat(4, 42px);
        column-gap: 13px;
        row-gap: 16px;
        margin-top: 20px;
        margin-bottom: 104px;
        @media screen and (max-width: 700px) {
            grid-template-columns: repeat(1, 1fr);
            margin-bottom: 80px;
        }

    }

    &__field {
        position: relative;
        &-first {
            visibility: hidden;
            animation: visible 2s linear 1s forwards;
        }
        &-last {
            visibility: hidden;
            animation: visible 2s linear 2s forwards;
        }
        &-nationality {
            visibility: hidden;
            animation: visible 2s linear 3s forwards;
        }
        &-mail {
            visibility: hidden;
            animation: visible 2s linear 4s forwards;
        }
        &-birdth {
            visibility: hidden;
            animation: visible 2s linear 5s forwards;
        }
        &-gender {
            visibility: hidden;
            animation: visible 2s linear 6s forwards;
        }
        &-password {
            visibility: hidden;
            animation: visible 2s linear 7s forwards;
        }
        &-confirm {
            visibility: hidden;
            animation: visible 2s linear 8s forwards;
        }
    }
    
    &__input {
        display: block;
        width: 100%;
        height: 27px;
        padding-bottom: 6px;
        border: none;
        border-bottom: 1px solid #f2f2f2;
        background-color: transparent;
        outline: 0;
        font-size: 14px;
        &-mail {
            position: relative;
        }
    }
    &__check {
        width: 11px;
        height: 8px;
        position: absolute;
        top: 62%;
        right: 10px;
        display: none;
    }
    // стилизация выпадающего списка
    &__select {
        width: 100%;
        min-width: 40px;
        font-size: 14px;
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        border: none;
        background-color: transparent;
        outline: none;
        cursor: pointer;
        border-bottom: 1px solid #f2f2f2;
        // Убираем стрелку в IE 10
        &__select::-ms-expand {
            display: none;
        }
        &-block {
            position: relative;
            &::before {
                @include pseudo;
                top: 8px;
                right: 0px;
                background-image: url("../images/row.svg");
                @include background;
                pointer-events: none;
                width: 8px;
                height: 4px;
            }
            &--day {
                max-width: 56px;
                width: 100%;
                @media screen and (max-width: 400px) {
                    max-width: 10%;
                }
            }
            &--month {
                max-width: 91px;
                width: 100%;
            }
            &--year {
                max-width: 68px;
                width: 100%;
            }
        }

        &-wrapp {
            display: flex;
            gap: 10px;
            max-width: 100%;
            @media screen and (max-width: 700px) {
                gap: 0;
                justify-content: space-between;
            }
        }
    }
    &__label {
        display: block;
        margin-bottom: 5px;
        font-family: 'Work SansLight', sans-serif;
        font-size: 13px;
        line-height: 14px;
        color: #7c7c7c;
    }
}

@keyframes visible {
    0% {
        visibility: hidden;
    }
    100% {
        visibility: visible;
    }
}
